<template>
  <header class="header">
    <div class="container">
      <div class="logo">
        <router-link to="/">Mr.Chen</router-link>
      </div>

      <ul class="music-nav clearfix">
        <li>
          <router-link :to="{path: ''}">1</router-link>
        </li>
        <li>
          <router-link :to="{path: ''}">2</router-link>
        </li>
        <li>
          <router-link :to="{path: ''}">3</router-link>
        </li>
        <li>
          <router-link :to="{path: ''}">4</router-link>
        </li>
        <li>
          <router-link :to="{path: ''}">5</router-link>
        </li>
        <li>
          <router-link :to="{path: ''}">6</router-link>
        </li>
        <li>
          <router-link :to="{path: ''}">7</router-link>
        </li>
        <li>
          <router-link :to="{path: ''}">8</router-link>
        </li>
        <li>
          <router-link :to="{path: ''}">9</router-link>
        </li>
      </ul>
    </div>
  </header>
</template>

<script>
  import Piano from '../../assets/js/piano-play'

  export default {
    name: "Header",
    created() {
      this.initMusicNav();
    },
    methods: {
      initMusicNav() {
        this.$nextTick(function () {

          // 初始化piano
          var piano = new Piano({
            playKeyCallback: navAnimate,
            // mediaSrc: mediaSrc
          });

          // hover弹奏
          $(".music-nav li").mouseover(function () {
            var index = $(this).index() + 1;
            piano.playKey(index);
            piano.record(index);
          })

          // 导航动画
          function navAnimate(key){
            var $menu = $('.music-nav li');
            $menu.eq(key-1).removeClass('on').addClass('on');
            setTimeout(function(){
              $menu.eq(key-1).removeClass('on');
            }, 200);
          }
        })
      }
    }
  }
</script>

<style scoped>
  @import "../../assets/css/header.css";
</style>



